.ratingselect .reting-type {
  padding: 18px 0;
  margin: 0 18px;
  font-size: 0;
}
.ratingselect .reting-type .block {
  display: inline-block;
  vertical-align: top;
  font-size: 12px;
  padding: 8px 12px;
  margin-right: 8px;
  border-radius: 1px;
  color: #4d555d;
}
.ratingselect .reting-type .block.positive {
  background: rgba(0, 160, 220, 0.2);
}
.ratingselect .reting-type .block.positive.active {
  background: #00a0dc;
  color: #FFFFFF;
}
.ratingselect .reting-type .block.negative {
  background: #ccd0d1;
}
.ratingselect .reting-type .block.negative.active {
  background: #4d555d;
  color: #FFFFFF;
}
.ratingselect .switch {
  padding: 3px 18px;
  line-height: 24px;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
  color: #93999f;
  font-size: 0;
}
.ratingselect .switch.on .icon-check_circle {
  color: #00c850;
}
.ratingselect .switch .icon-check_circle {
  margin-right: 4px;
  font-size: 24px;
}
.ratingselect .switch .text {
  display: inline-block;
  vertical-align: top;
  font-size: 12px;
}



