.cartcontrol {
  display: inline-block;
  padding: 6px;
  transition: all 0.4s linear;
}
.cartcontrol .inner {
  display: inline-block;
  line-height: 24px;
  font-size: 24px;
  color: #00a0dc;
}
.cartcontrol .cart-decrease {
  display: inline-block;
  padding: 6px;
}
.cartcontrol .cart-count {
  display: inline-block;
  vertical-align: top;
  font-size: 10px;
  color: #93999f;
  line-height: 24px;
  text-align: center;
  padding: 6px 0;
}
.cartcontrol .cart-add {
  display: inline-block;
  vertical-align: top;
  font-size: 24px;
  color: #00a0dc;
  line-height: 24px;
  padding: 6px;
}



