#food {
  z-index: 10;
  width: 100%;
  background-color: #FFFFFF;
}
#food .icon-thumb_up {
  color: #1392cf;
}
#food .title {
  font-size: 18px;
  line-height: 18px;
  margin-bottom: 8px;
  font-weight: 800;
  color: #07111b;
}
#food .food-content {
  background: #ffffff;
  position: relative;
}
#food .image-header {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 100%;
}
#food .image-header img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
#food .image-header .back {
  position: absolute;
  top: 10px;
  left: 5px;
}
#food .image-header .back .icon-arrow_lift {
  display: block;
  padding: 10px;
  font-size: 20px;
  font-weight: 900;
  color: #FFFFFF;
}
#food .content {
  width: 100%;
  padding: 18px;
}
#food .content .detail {
  margin-bottom: 18px;
  line-height: 10px;
  /*font-size: 0;*/
}
#food .content .rating {
  margin-right: 12px;
  font-size: 10px;
  color: #99999f;
}
#food .content .sell-sount {
  margin-right: 12px;
  font-size: 10px;
  color: #99999f;
}
#food .content .price {
  font-weight: 700;
  line-height: 24px;
}
#food .content .price .now {
  margin-right: 8px;
  font-size: 14px;
  color: #f01414;
}
#food .content .price .old {
  text-decoration: line-through;
  color: #93999f;
  font-size: 11px;
}
#food .cartcontrol-wrapper {
  position: absolute;
  right: 10px;
  bottom: 10px;
}
#food .buy {
  position: absolute;
  right: 18px;
  bottom: 18px;
  z-index: 10;
  height: 24px;
  line-height: 24px;
  font-size: 10px;
  padding: 0 12px;
  box-sizing: border-box;
  border-radius: 12px;
  background-color: #3689ef;
  color: #FFFFFF;
}
#food .info {
  padding: 18px;
}
#food .info .tltle {
  font-size: 14px;
  line-height: 24px;
  padding: 0 8px;
  color: #07111b;
}
#food .rating {
  padding: 18px;
}
#food .rating-wrapper {
  width: 100%;
  margin: 14px;
}
#food .rating-wrapper .rating-item {
  height: 100px;
}
#food .rating-wrapper .rating-item .user {
  position: relative;
  float: right;
  margin-right: 30px;
  margin-top: 10px;
}
#food .rating-wrapper .rating-item .user .name {
  top: 0;
  margin-bottom: 10px;
}
#food .rating-wrapper .rating-item .user .avatar {
  width: 50px;
}
#food .rating-wrapper .rating-item .text {
  font-size: 12px;
}
#food .text {
  line-height: 24px;
  padding: 0 8px;
  font-size: 12px;
  color: #4d555d;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
.list-enter-active,
.list-leave-active {
  transition: all 1s;
}
.list-enter,
.list-leave-active {
  opacity: 0;
  transform: translateX(100px);
}
.icon-thumb_down {
  color: #434a52;
}



