
@font-face {
  font-family: 'sell-icon';
  src: url('../../assets/css/fonts/sell-icon.eot?b07ezg');
  src: url('../../assets/css/fonts/sell-icon.eot?b07ezg#iefix') format('embedded-opentype'), url('../../assets/css/fonts/sell-icon.ttf?b07ezg') format('truetype'), url('../../assets/css/fonts/sell-icon.woff?b07ezg') format('woff'), url('../../assets/css/fonts/sell-icon.svg?b07ezg#sell-icon') format('svg');
  font-weight: normal;
  font-style: normal;
}
[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'sell-icon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-add_circle:before {
  content: "\e900";
}
.icon-arrow_lift:before {
  content: "\e901";
}
.icon-check_circle:before {
  content: "\e902";
}
.icon-close:before {
  content: "\e903";
}
.icon-favorite:before {
  content: "\e904";
}
.icon-keyboard_arrow_right:before {
  content: "\e905";
}
.icon-remove_circle_outline:before {
  content: "\e906";
}
.icon-shopping_cart:before {
  content: "\e907";
}
.icon-thumb_down:before {
  content: "\e908";
}
.icon-thumb_up:before {
  content: "\e909";
}
ul li {
  list-style: none;
}
#combos {
  z-index: 10;
  width: 100%;
  min-height: 100%;
}
#combos .welcome .title {
  position: fixed;
  display: inline-block;
  vertical-align: top;
  text-align: center;
  width: 100%;
  height: 45px;
  line-height: 45px;
  color: #FFFFFF;
  background: linear-gradient(45deg, #49aa83 0%, #3d8d8e 100%);
  font-size: 18px;
  z-index: 11;
}
#combos .welcome .title .back {
  float: left;
  margin-top: 3px;
  margin-left: 5px;
}
#combos .content {
  top: 50px;
  width: 95%;
  margin: 0 auto;
}
#combos .content .text {
  margin-top: 5px;
  height: 60px;
  color: #bababa;
  font-size: 18px;
  line-height: 18px;
}
#combos .Products {
  width: 95%;
  margin: 0 auto;
  /*选中*/
}
#combos .Products .groupItem {
  margin-left: 5%;
}
#combos .Products .item {
  margin: 3px;
  display: inline-block;
  width: 29%;
  height: 70px;
}
#combos .Products .noactive {
  border-top: 3px solid #a0a4a8;
  border-right: 3px solid  #a0a4a8;
  border-bottom: 3px solid #a0a4a8;
  border-left: 3px solid #a0a4a8;
  background-color: #a0a4a8;
}
#combos .Products .active {
  border-top: 3px solid #236194;
  border-right: 3px solid #236194;
  border-bottom: 3px solid #236194;
  border-left: 3px solid #236194;
}



